import React from 'react';
import './tweet.css';
import img from './assets/shaggy.png';

export const StaticTweet = () => {
    return (
        <div className="tweet">
        <Avatar/>
        <div className="content">
          <Author/><Time/>
          <Message/>
          <div className="buttons">
            <ReplyButton/>
            <RetweetButton/>
            <LikeButton/>
            <MoreOptionsButton/>
          </div>
        </div>
      </div>
  
    )
}

function Message() {
  return (
    <div className="message">
      Scooby Doo, Where Are You?
    </div>
  );
}

function Author() {
  return (
    <span className="author">
      <span className="name">Shaggy</span>
      <span className="handle">@shaggy</span>
    </span>
  );
}

function Avatar() {
  return (
    <img
      src={img}
      className="avatar"
      alt="avatar" />
  );
}

const Time = () => (
  <span className="time">3m ago</span>
);

const ReplyButton = () => (
  <i className="fa fa-reply reply-button"/>
);

const RetweetButton = () => (
  <i className="fa fa-retweet retweet-button"/>
);

const LikeButton = () => (
  <i className="fa fa-heart like-button"/>
);

const MoreOptionsButton = () => (
  <i className="fa fa-ellipsis-h more-options-button"/>
);

